}
.title table {
  text-align: center;
}
.title table tr {
  height: 30px !important;
  font-size: 14px !important;
  line-height: 30px;
}
.title table tr:nth-child(odd) {
  background: #f4f4f4;
}
.title table tr:nth-child(5) {
  background: #f2f6fa;
  color: #fff;
}



