
@charset "UTF-8";
/*  */
.spacing {
  padding: 0 !important;
}
.navigationBackground {
  background-color: #d3e5f7;
}
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
}
.container .header {
    height: 60px;
    line-height: 60px;
    background: white;
    color: #fff;
    border-bottom: 1px solid #bdd4e9;
}
.container .header .userinfo {
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      text-decoration: none;
      text-align: right;
      height: 60px;
      padding-right: 35px;
      color: black;
      float: right;
}
.container .header .userinfo .userinfo-inner {
        cursor: pointer;
        color: #fff;
}
.container .header .userinfo .userinfo-inner img {
          width: 13px;
          height: 13px;
          float: right;
          margin-right: 8px;
          margin-left: 15px;
}
.container .header .logo {
      height: 60px;
      font-size: 22px;
      padding-left: 20px;
      padding-right: 20px;
      border-color: rgba(238, 241, 146, 0.3);
      border-right-width: 1px;
      border-right-style: solid;
}
.container .header .logo img {
        width: 100%;
        height: 60%;
        margin: 10px 10px 10px 10px;
}
.container .header .logo .txt {
        color: #fff;
}
.container .header .logo-width {
      width: 230px;
}
.container .header .logo-collapse-width {
      width: 60px;
}
.container .header .tools {
      padding: 0px 20px;
      width: 300px;
      height: 60px;
      color: black;
      font-family: ;
      font-size: 18px;
      line-height: 60px;
      cursor: pointer;
}
.container .main {
    display: flex;
    position: absolute;
    top: 60px;
    bottom: 0px;
    overflow: hidden;
}
.container .main aside {
      flex: 0 0 230px;
      width: 230px;
}
.container .main aside .el-menu {
        height: 100%;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        -moz-background-size: 100% 100%;
}
.container .main aside .collapsed {
        width: 60px;
}
.container .main aside .collapsed .item {
          position: relative;
}
.container .main aside .collapsed .submenu {
          position: absolute;
          top: 0px;
          left: 60px;
          z-index: 99999;
          height: auto;
          display: none;
}
.container .main .menu-collapsed {
      flex: 0 0 60px;
      width: 60px;
}
.container .main .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
}
