
@charset "UTF-8";
html, body {
  height: 100%;
}
body {
  margin: 0;
}
.container-home {
  width: 100%;
  height: 100%;
  background-color: #eef5fc;
  padding: 0px;
  font-size: 14px;
  /**/
  margin-top: 0px;
  margin-bottom: 0px;
  /*100%*/
  height: 100%;
  float: left;
}
.content-daiban {
  width: 49.5%;
  height: 30%;
  min-height: 200px;
  background-color: #fff;
  float: left;
}
.content-account {
  width: 49.5%;
  margin-left: 1%;
  height: 30%;
  min-height: 200px;
  background-color: #fff;
  float: left;
}
.content-gonggao {
  width: 49.5%;
  height: 30%;
  margin-top: 10px;
  min-height: 200px;
  background-color: #fff;
  float: left;
}
.content-customer {
  width: 49.5%;
  margin-left: 1%;
  margin-top: 10px;
  height: 30%;
  min-height: 200px;
  background-color: #fff;
  float: left;
}
.content-daili {
  width: auto;
  left: 250px;
  right: 38px;
  top: 450px;
  bottom: 30px;
  height: auto;
  background-color: #fff;
  position: absolute;
  overflow-y: auto;
}
.title {
  width: 100%;
  height: 40px;
  line-height: 40px;
  margin-bottom: 6px;
}
.titleLeft {
  width: 58%;
  height: 100%;
  text-align: left;
  float: left;
  padding-left: 2%;
}
.titleRight {
  width: 38%;
  height: 100%;
  text-align: right;
  float: left;
  padding-right: 2%;
}
.titleLeft2 {
  width: 59%;
  height: 100%;
  text-align: left;
  float: left;
  padding-left: 1%;
}
.titleRight2 {
  width: 39%;
  height: 100%;
  text-align: right;
  float: left;
  padding-right: 1%;
}
.daiban-content {
  width: 100%;
  height: 30px;
  line-height: 30px;
}
.account-content {
  width: 100%;
  height: 150px;
}
.hr1 {
  width: 96%;
  height: 1px;
  border: none;
  border-top: 1px solid #F3F6FC;
}
.hr2 {
  width: 96%;
  height: 1px;
  border: none;
  border-top: 1px dashed #F1F1F1;
}
.accountmain {
  width: 80%;
  margin-left: 10%;
  height: 120px;
  margin-top: 15px;
  border-right: none;
}
.accountleft {
  width: 40%;
  height: 100%;
  float: left;
  text-align: right;
  padding-right: 10%;
  margin-top: 8px;
}
.accountright {
  width: 50%;
  height: 100%;
  float: left;
  margin-top: 8px;
}
.accountright1 {
  width: 100%;
  height: 49%;
  line-height: 30px;
}
.accountright2 {
  width: 100%;
  height: 49%;
  color: blue;
}
.title table {
  text-align: center;
}
.title table tr {
  height: 30px !important;
  font-size: 14px !important;
  line-height: 30px;
}
.title table tr:nth-child(odd) {
  background: #F4F4F4;
}
.title table tr:nth-child(5) {
  background: #F2F6FA;
  color: #FFF;
}



