
.container-login .container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.login-container {
  margin-left: 70%;
  margin-top: 16%;
  width: 20%;
  padding: 35px 35px 15px 35px;
  background: transparent;
}
.login-container .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
}
.login-container .remember {
    margin: 0px 0px 35px 0px;
}
.icon-user-input {
  position: absolute;
  left: 4%;
  top: 70%;
  background-image: url(../assets/userIcon.png);
  background-size: 24px 30px;
  background-repeat: no-repeat;
  background-position: 0px 3px;
  width: 24px;
  height: 35px;
  z-index: 777;
}
.icon-clean-account {
  float: right;
  position: absolute;
  right: 4%;
  top: 80%;
  background-image: url(../assets/loginCleanIcon.png);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 5px 3px;
  width: 31px;
  height: 30px;
  z-index: 888;
  visibility: hidden;
}
.icon-password-input {
  position: absolute;
  left: 4%;
  top: 160%;
  background-image: url(../assets/passWordIcon.png);
  background-size: 24px 30px;
  background-repeat: no-repeat;
  background-position: 0px 3px;
  width: 24px;
  height: 35px;
  z-index: 999;
}
.login-user-input .el-input__inner {
  padding-left: 50px;
  position: absolute;
  font-size: 18px;
  border: 0px;
  z-index: 333;
  height: 50px;
  width: 100%;
  margin-top: -5px;
  background: url(../assets/loginInputBg.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.login-password-input .el-input__inner {
  padding-left: 51px;
  position: absolute;
  font-size: 18px;
  border: 0px;
  height: 50px;
  z-index: 333;
  margin-top: 28px;
  width: 100%;
  background: url(../assets/loginInputBg.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.account-login .el-form-item__error {
  display: none;
  font-size: 15px;
  top: 65px;
}
.password-login .el-form-item__error {
  font-size: 15px;
  display: none;
  top: 98px;
}
.bgmImg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.loginTiittle {
  font-weight: 600;
  font-size: 25px;
  font-family: Helvetica;
  color: #303030;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}



